.team-stagger-section {
    .staggered-details-contents {
        max-width: 100%;
        .person-details {
            margin-top: 1.5rem;
            margin-bottom: 1.5rem;
            display: flex;
            align-items: center;
        }
        .fa-linkedin {
            margin: 1rem 3rem;
            position: relative;
            color: #fff;
            &:after {
                content: '';
                background: $lightblue;
                position: absolute;
                top: 0;
                left: 0;
                padding: 1rem;
                margin: -0.5rem -0.6rem;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                z-index: -1;
            }
        }
        h2 {
            @include omnes_medium;
            font-size: 1.25rem;
            margin-bottom: 0.25rem;
        }

        h3 {
            @include omnes-regular;
            text-transform: uppercase;
            font-size: 0.8rem;
            color: $lightblue;
        }
    }
    .staggered-image {
        img {
            // object-fit: contain;
            // object-position: top;
            @include object-fit(contain top);
        }
    }
}

.team-tab-contents {

    article {
        section:first-child {
            margin-top: 1rem
        }
    }
    > .staggered-features.tabs-panel {
        @include breakpoint(medium up) {
            margin: 2rem 0;
        }
        margin: 1rem 0;

        padding: 0rem;

    }
}

.team-tab-container {
    margin-top: 4rem;

    @include breakpoint(medium down) {
        margin-top: 2rem;
    }

    @include breakpoint(small down) {
        margin-top: 1rem;
        overflow-x: scroll;
    }
}

// Special rule for charter section
#charter-panel .team-stagger-section .staggered-image img {
    @include object-fit(contain);
    object-position: top;
}

#team-tabs {
    overflow: hidden;
}

