footer {
    background: $blue;
    > .row {
        position: relative;
    }
    .footer-contents {
        @include breakpoint(small down) {
            display: flex;
            flex-direction: column-reverse;
        }
    }
}

.footer-nav, .footer-highlights {
}

.social-wrapper {
    padding-top: 40px;
    padding-bottom: 20px;

    @include breakpoint(small down) {
        padding-top: 20px;
    }

    & > span, & > a {
        display: block;
        color: #fff;
        font-size: 0.9375em;
        line-height: 1em;
        text-transform: uppercase;
        margin-bottom: 15px;

        @include omnes_medium;
    }
}

.footer-nav{
    padding-top: 40px;
    padding-bottom: 20px;
    display: inline-block;
    column-count: 4;



    nav {
        break-inside: avoid;
        & > span, & > a {
            display: block;
            color: #fff;
            font-size: 0.9375em;
            line-height: 1em;
            text-transform: uppercase;
            margin-bottom: 15px;

            @include omnes_medium;
        }
    }

    ul {
        padding-bottom: 10px;
    }

    li {
        @include omnes-regular;
        font-size: 0.75em;
        line-height: 1em;
        text-transform: uppercase;
        margin-bottom: 15px;

        a {
            color: $lightblue;
            text-decoration: none;
        }
    }


    .footer-nav-first-column {
        // @include breakpoint( medium only ) {
        //  margin-left: 10px;
        // }

        // @include breakpoint( large ) {
        //  margin-left: 5%;
        // }
    }

    nav {
        @include breakpoint(small down) {
            width: auto;
        }
    }
}

.footer-highlights-wrapper {
    border-bottom: 1px solid $lightblue;
