.new-sign-up {
    
    .newsletter-header {

        h2{
            @include omnes_light;
            color: #fff;
            font-size: 1.8rem;
            margin: 1rem 0;
            letter-spacing: 0.1rem;
        }

        span{
            @include omnes-regular;
            color: #fff;
        }
    }

    select {
        font-size: 15px;
        height: 100%;
    }

    .select2 {
        width: 100% !important;
        height: 100%;
        padding: 0.5rem;
        border: none;
        padding: 0px;
        // margin: 0 0 1rem;
        font-size: 1rem;
        color: #0a0a0a;
        background-color: #fff;
    }

    &:first-child .select2 {
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
    }

    .select2-selection__arrow {
        height: 100%;
        position: absolute;
        top: 0;
        right: 1px;
        width: 20px;
        bottom: 0;
        margin: auto;

        background-position: center;
        background-repeat: no-repeat;
        background-image: url(/assets/images/select_down.png);
    }

    b {
        display: none;
    }

    .select2-selection {
        border: none;
        outline: none;
    }

    .select2-container--default {
        text-align: left;
        display: flex;
        align-items: center;

        .select2-results__option[aria-selected=true] {
            background-color: #f2f5f6;
            color: #747678;
        }

        .select2-results__option--highlighted[aria-selected] {
            background-color: red ;
        }

        .select2-results__option--highlighted[aria-selected] {
            background-color: #5897fb;
            color: white;
        }

        .select2-results__option[aria-selected=true] {
            background-color: #ddd;
        }

        .selection {
            width: 100%;
        }

        .select2-selection--single {
            height: auto;
            .select2-selection__rendered {
                line-height: initial;
                @include omnes-regular;
                font-size: 1.25rem;
                color: black;
                @include breakpoint(medium down) {
                    padding-left: 0px;
                }

            }
        }
    }


    @keyframes flip
    {
      0% {
        transform:rotateX(0deg);
        animation-timing-function:ease-out;
      }

      40% {
        transform:rotateX(-10deg);
        animation-timing-function:ease-out;
      }

      70% {
        transform:rotateX(220deg);
        animation-timing-function:ease-in;
      }

      100% {
        transform:rotateX(180deg);
        animation-timing-function:ease-in-out;
      }
    }

    @keyframes pop
    {
      0% {
        transform: scale(0);
        opacity: 0;
      }
      20% {
        opacity: 1;
      }
      100% {
        transform: scale(1);
        opacity: 1;
      }
    }

    // .btn,button,input[type=submit] {
    //   background:#45d1e5;
    //   color:#fff;
    //   font-weight:700;
    //   position:relative;
    //   border-radius:6px;
    //   min-width:4em;
    //   text-indent:0;
    //   overflow:hidden;
    //   transform-style:preserve-3d;
    //   transition:all 200ms cubic-bezier(0.86,0,.07,1);
    // }

    $animationSpeed: 1.2s;

    .loader-container{
        display: flex;
        justify-content: center;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0;
        @include transition(opacity 250ms);
        .loader {
            align-self: center;
            position: relative;
            text-indent: -9999em;
            line-height: 0;
            height: 8px;
            .ball {
                border-radius: 50%;
                background: white;
                width: 8px;
                height: 8px;
                position: absolute;
                top: 0;
                left: 0;
                animation: loading $animationSpeed linear infinite;
                &:nth-of-type(2) {
                    animation-delay: $animationSpeed / 3;
                }
                &:nth-of-type(3) {
                    animation-delay: $animationSpeed / 3 * 2;
                }
            }
        }       
    }

    .is-loading{
        .loader-container{
            opacity: 1;
        }
    }

    .is-loaded{
        .loader-container{
            opacity: 0;
        }
    }


    @keyframes loading {
        0% {
            opacity: 0;
            transform: scale(0.5);
        }
        20% {
            opacity: 1;
            transform: scale(1);
        }
        40% {
            transform: translate3d(16px, 0, 0);
        }
        60% {
            transform: translate3d(33px, 0, 0);
        }
        80% {
            transform: translate3d(50px, 0, 0);
            opacity: 1;
        }
        100% {
            transform: translate3d(50px, 0, 0) scale(0.5);
            opacity: 0;
        }
    }



    .btn.is-loading:after,button.is-loading:after,input.is-loading[type=submit]:after {
        opacity: 1;
      }

    .btn.is-loaded:after,button.is-loaded:after,input.is-loaded[type=submit]:after {
      opacity:0;
    }

    input[type=color],input[type=date],input[type=datetime-local],input[type=datetime],input[type=email],input[type=month],input[type=number],input[type=password],input[type=search],input[type=tel],input[type=text],input[type=time],input[type=url],input[type=week],select,textarea {
      border:none;
      box-shadow:none;
      background-color:#fff;
      border-radius:0!important;
      width:100%;
      // padding:.5em 1em;
      padding: 0em;
      margin: 0em;
      height: 1.8rem;
    }

    label {
      display:block;
      margin-bottom:.5em;
    }


    .sign-up {
      margin:2em 0;
      position:relative;
      perspective: 1000px;
      text-align:center;
    }

    .sign-up-form {
        max-width:30em;
        transform-style: preserve-3d;
        backface-visibility: hidden;
        transform: translate3d(0,0,0);
        margin:0 auto;
    }

    .no-flexbox .field {
      width:30%;
      float:left;
    }

    .no-flexbox .sign-up-btn {
      float:left;
    }

    .field {

        height: 3rem;

        * {
            color: black;
        }

        zoom:1;
        display:block;
        background:#fff;
        position:relative;
        // border:1px solid $grayborder;
        transform: translate3d(0,0,0);
        overflow:visible;
        margin:0;
        padding:0 .5em;

        @include breakpoint( medium down) {
            border-bottom: 1px solid $grayborder;
            &:last-of-type input {
                border-bottom: 0px;
            }

        }

        &:last-of-type input {
            border-bottom:none;
        }
        &::nth-of-type(1) {
            border-bottom:none;
            border-radius:2px 2px 0 0;
        }
        &:nth-of-type(2),.field:nth-of-type(3) {
            @include breakpoint( large up) {
                border-top:none;
                border-bottom:none;
                border-radius:0;            
            }

        }
        // &:nth-of-type(3) input {
        //  border-right:none;
        // }
        &.error:after {
            content:"!";
            line-height:0.6em;
            background:#fff;
            border:2px solid #d2244b;
            color: #d2244b;
            border-radius:100%;
            display:inline-block;
            position:absolute;
            top:0;
            bottom:0;
            right:1.25rem;
            height:1.2em;
            width:1.2em;
            vertical-align:middle;
            text-align:center;
            animation: pop 200ms forwards cubic-bezier(0.175,.885,.32,1.275);
            margin:auto;
            padding:.2em;
            &:nth-of-type(1):after {
                animation-duration: 100ms;
            }
            &:nth-of-type(2):after {
                animation-duration:200ms;
            }
            &:nth-of-type(3):after {
                animation-duration:300ms;
            }
        }
    }

    input[type=email],input[type=text] {
        display:block;
        border-bottom: 0px solid $grayborder;
        line-height: 1.5em;
        padding: 1em 0;
    }

    .sign-up-btn {
        width:100%;
        margin: 0px;
        overflow: hidden;
        position: relative;

        transform-style: preserve-3d;
        backface-visibility: hidden;
        &.is-loading {
            color: rgba(255, 255, 255, 0);
        }
    }

    .sign-up-success {
        position:absolute;
        bottom:0;
        top: 0;
        left:0;
        right:0;
        transform: rotate3d(1,0,0,-180deg) translateZ(-1px);
        transform-style: preserve-3d;
        backface-visibility: hidden;
        margin:auto;
        max-width: 100%;
        width: 100%;
    }

    .flipsies {
      position:relative;

      // This won't work for child elements in IE
      transform-style: preserve-3d;
    }

    .is-complete .flipsies {
      animation: flip 800ms forwards;
    }

    .is-complete .sign-up-form {
        transform: translate3d(0,0,0) translateZ(2px);
    }

    .is-complete .sign-up-success {
      transform: rotate3d(1,0,0,-180deg) translateZ(-1px);

      // Needed to at least make it visible in IE.
      -ms-backface-visibility: visible;
    }

    @include breakpoint( large up ) {
      .sign-up {
        margin-bottom:1em;
      }
    }

    @include breakpoint( large up ) {
        .btn,button,input[type=submit] {
            border-radius:0 2px 2px 0;
        }

        .sign-up-form {

            max-width:100%;
            display: flex;
            box-pack: center;
            justify-content:center;
            overflow:hidden;
            transform:translate3d(0,0,0);
            padding:0;
        }
        .field {
            // border:1px solid $grayborder;
            flex:1;
            margin:0;
            display: flex;
            align-items: center;
        }

        .field:nth-of-type(1) {
            // border-top:1px solid $grayborder;
            border-right:none;
            // border-bottom: 0px solid $grayborder;
            // border-left:1px solid $grayborder;
            border-radius:2px 0 0 2px;
        }

        .field:nth-of-type(2),.field:nth-of-type(3) {
            // border-top:1px solid $grayborder;
            border-right:none;
            // border-bottom: 0px solid $grayborder;
            border-left:none;
            border-radius:0;
        }

        // .field:nth-of-type(3) input {
        //  border-right:none;
        // }

        input[type=email],input[type=text] {
            display:inline-block;
            border-top:none;
            border-left:none;
            border-bottom:none;
            border-right:1px solid $grayborder;
            border-radius:0;
            margin: 0;
            // padding: 0;
            padding: 0 0.5rem;
        }

        .sign-up-btn {
            border-left:none;
            width:auto;
            border-radius:0 2px 2px 0;
        }

        .sign-up-success {
            padding:.7em 2em;
        }
    }
}

