.lg-close {
  width: 22px;
  height: 22px;
  background: url('/assets/images/cross.png') no-repeat 0 0;
  float: right;
  margin: 16px 40px 0 0;
}

.gallery-logo {
  display: block;
  width: 55px;
  height: 55px;
  background: url('/assets/images/logo_small.png') no-repeat center center;
  background-size: contain;
  float: left;
}

.lg-toolbar-bot {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 55px;
  width: 100%;
  background-color: #fff;
  z-index: 1080;
}

.gallery-yacht {
  float: left;
  display: block;
  @include omnes_light;
  line-height: 1em;
  text-transform: uppercase;
  padding: 19px 0 0 70px;

  span {
    @include trajan;
    font-size: 0.9375em;
  }
}

.gallery-share {
  position: absolute;
  top: 0;
  right: 0;
  border-left: 1px solid $lightgrey;
  height: 100%;

  a {
    display: block;
    width: 100px;
    background: url('/assets/images/share_light.png') no-repeat center center;
    height: 100%;
  }
}

.gallery-download {
  position: absolute;
  top: 0;
  right: 101px;

  border-left: 1px solid $lightgrey;
  height: 100%;

  a {
    display: block;
    padding: 22px 40px 22px 72px;
    background: url('/assets/images/download.png') no-repeat 39px center;
    height: 100%;
    @include omnes_medium;
    font-size: 0.75em;
    line-height: 1em;
    text-transform: uppercase;
    text-decoration: none;
    color: $grey;
  }
}

.gallery-caption {
  font-size: 0.75em;
  line-height: 1.33333333em;
  @include omnes-regular;
  color: $grey;
  padding-top: 20px;
  float: left;
}

.lg-thumb-item img {
  opacity: 0.25;
  transition: opacity 0.3s;
}

.lg-thumb .active img, .lg-thumb-item:hover img {
  opacity: 1;
}

.gallery-sections {
  float: right;
  margin: 10px 40px 0 0;

  a {
    font-size: 0.75em;
    line-height: 1em;
    text-transform: uppercase;
    color: $blue;
    @include omnes_medium;
    display: inline-block;
    padding: 11px 20px 10px;
    border: 1px solid $blue;
    cursor: pointer;

    &:first-child {
      -webkit-border-top-left-radius: 3px;
      -webkit-border-bottom-left-radius: 3px;
      -moz-border-radius-topleft: 3px;
      -moz-border-radius-bottomleft: 3px;
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px;
    }

    &:last-child {
      -webkit-border-top-right-radius: 3px;
      -webkit-border-bottom-right-radius: 3px;
      -moz-border-radius-topright: 3px;
      -moz-border-radius-bottomright: 3px;
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
    }

    &.gallery-active {
      background: $blue;
      color: #fff;
    }
  }
}

